*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto Slab', serif;
    font-size: xx-large;
    text-decoration: none;
    color: gray;
}

.big-wrapper{
    display: grid;
    grid-auto-flow: column;
    background-color: black;

    #nav-bar{
        display: flex;
        flex-direction: column;
        background-color: #28282B;
    }
}